/* eslint-disable no-plusplus */

import { asideTemplate } from '../templates/templateCreator';

class Aside extends HTMLElement {
  set title(title) {
    this._title = title;
    // console.log(this._title);
  }

  set data(data) {
    this._data = data;
    this.render();
    this.afterRender();
    // console.log(this._data);
  }

  async render() {
    this.innerHTML = `
      <div class="suggestion bg-slate-800 p-2 text-slate-50 rounded w-full mb-5">
        <div class="title mb-2 px-2">
          <h2 class="font-bold text-lg">${this._title}</h2>
        </div>
        <div class="aside-list flex flex-col gap-2 h-[500px] lg:h-fit lg:max-h-[calc(100vh-220px)]  overflow-y-scroll scrollbar-custom pt-1">

        </div>
      </div>
    `;
  }

  async afterRender() {
    const asideList = this.querySelector('.aside-list');

    this._data.slice(0, 9).forEach((tv) => {
      asideList.innerHTML += asideTemplate(tv, this._title);
      // console.log(tv);
    });
  }
}

customElements.define('aside-tag', Aside);
